import { Component, OnInit } from '@angular/core';
import { PictureTileModel } from './picture-tile/picture-tile.component';
import { MemoryService } from './memory-service';

@Component({
  selector: 'body',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less']
})
export class AppComponent implements OnInit{
  
  private tileModels:PictureTileModel[]=[];
  private playing: boolean = false;
  private lose: boolean = false;
  private winner: boolean = false;
  private countDowntimeout: number = 35000;
  private timer:any;
  constructor(private memoryService:MemoryService){
    this.initializeTiles();
  }

  private shuffle (array:any[]) {
    let i = 0
      , j = 0
      , temp = null

    for (i = array.length - 1; i > 0; i -= 1) {
      j = Math.floor(Math.random() * (i + 1))
      temp = array[i]
      array[i] = array[j]
      array[j] = temp
    }
  }

  ngOnInit(){
  }

  onTileClick(event:PictureTileModel){
    this.memoryService.evaluate(event);
    this.checkIfWin();
    if(!this.playing && !this.lose && !this.winner){
      this.playing = true;
      this.timer = setTimeout(()=>{
        this.lose = true;
        this.playing = false;
      },this.countDowntimeout);
      console.log(this.timer);
    }
  }

  initializeTiles():void{
    
    this.tileModels = [];
    //populate
    for(let number=0;number<9;number++){
      let tileModel = new PictureTileModel("TILE"+number);
      let tileModelCopy = new PictureTileModel("TILE"+number);
      this.tileModels.push(tileModel);
      this.tileModels.push(tileModelCopy);
    }
    this.shuffle(this.tileModels);
    console.log(this.tileModels);
    this.memoryService.setModels(this.tileModels);
  }

  restart():void{
    this.initializeTiles();
    this.lose = false;
    this.playing = false;
  }

  checkIfWin(): void{
    this.winner = false;
    for(const i in this.tileModels){
      const model = this.tileModels[i];
      if(!model.finished){
        return;
      }
    }
    this.playing = false;
    this.winner = true;
    clearTimeout(this.timer);
    clearInterval(this.timer);
  }
}



