import { Component, Input, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';

@Component({
  selector: 'picture-tile',
  templateUrl: './picture-tile.component.html',
  styleUrls: ['./picture-tile.component.less']
})
export class PictureTileComponent implements OnChanges{
  @Input() model:PictureTileModel;
  @Input() index:number;
  @Output() tileClick: EventEmitter<PictureTileModel>;

  constructor(){
    this.tileClick = new EventEmitter<PictureTileModel>();
    this.model;
  }

  ngOnChanges(change:SimpleChanges){
    if(change['index']){
      if(change['index'].currentValue){
        this.model.index = change['index'].currentValue;
      }
    }
  }

  onTileClick(){
    if(!this.model.evaluating){
      this.model.evaluating = true;
      setTimeout(()=>{
          this.tileClick.emit(this.model);
      },500);
      console.log(this.model.tileId);
      console.log(this.index);
    }
  }


}

export class PictureTileModel{
  public tileId:string;
  public index:number;
  public evaluating: boolean = false;
  public finished: boolean = false;
  public imgFileName:string;

  constructor(tileId?:string){
    if(tileId){
      this.tileId = tileId;
      let sequence = Number(this.tileId.substr(4)) + 1;
      let imgID = sequence > 9 ? sequence : "0" + sequence.toString();
      this.imgFileName = "robot" + imgID + ".png";      
      
    }
  }
}


