import { Component, Injectable } from '@angular/core';
import { PictureTileModel } from './picture-tile/picture-tile.component';
import { Subject } from 'rxjs/Rx';
@Injectable()
export class MemoryService {

    private compareLimit:number = 2;
    private tileModels:Array<PictureTileModel>;
    private compareModels:Array<PictureTileModel>=[];
    private onWin: Subject<boolean>;
    constructor(){
        this.onWin = new Subject<boolean>();
    }

    public setModels(tileModels:Array<PictureTileModel>):void{
        this.tileModels = tileModels;
    }


    public evaluate(tileModel: PictureTileModel): void{
        this.compareModels.push(tileModel);
        console.log("evaluate");
        console.log(this.compareModels);
        if(this.compareModels.length == this.compareLimit){
            let firstModel = this.compareModels[0];
            let notSame: boolean = false;
            for(let index in this.compareModels){
                if(firstModel.tileId != this.compareModels[index].tileId){
                    notSame = true;
                }
            }
            if(notSame){
                // alert("this is not same kids");
                for(let index in this.compareModels){
                    this.compareModels[index].evaluating = false;
                }
            }else{
                for(let index in this.compareModels){
                    //You would want to complete this ones
                    this.compareModels[index].finished = true;

                }
            }
            this.compareModels=[];
        }
    }
    
}


